import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-curb65',
  templateUrl: './curb65.component.html',
  styleUrls: ['./curb65.component.css']
})
export class CURB65Component implements OnInit {
  Points: number = 0; /* Default Points */

  ngOnInit(): void {
    this.Reset() /* Runs Reset on page load */
  }

  Print() { /* Prints the website to PDF */
    window.print();
  }

  Reset() { /* Resets radio buttons to default*/
    (<HTMLInputElement>document.getElementById("criteria_1_1-no")).checked = true;
    (<HTMLInputElement>document.getElementById("criteria_1_2-no")).checked = true;
    (<HTMLInputElement>document.getElementById("criteria_1_3-no")).checked = true;
    (<HTMLInputElement>document.getElementById("criteria_1_4-no")).checked = true;
    (<HTMLInputElement>document.getElementById("criteria_1_5-no")).checked = true;

    /*(<HTMLInputElement>document.getElementById("pneumonia-yes")).checked = true;  Not sure if i want this ? */

    this.Points = 0; /* Resets score to default*/
    this.CheckPneumonia()
  }

  CheckPneumonia() { /* Checks if Pneumonia or AnyInfection radio button is checked and hides/shows the approproate text */
    if ((<HTMLInputElement>document.getElementById("pneumonia-no")).checked === true)
  {
    (<HTMLInputElement>document.getElementById("Pneumonia")).style.display = 'none';
    (<HTMLInputElement>document.getElementById("AnyInfection")).style.display = 'block';
    this.CheckTxtResult_1_2()
    this.CheckS_1_1()
  }
  else
  {
    (<HTMLInputElement>document.getElementById("Pneumonia")).style.display = 'block';
    (<HTMLInputElement>document.getElementById("AnyInfection")).style.display = 'none';
    this.CheckTxtResult_1_1()
    this.CheckS_1_1()
  }}

  Add() { /* For Addition of points, Prints new result and starts check */
    this.Points++;
    this.CheckPneumonia()
  }

  Sub() { /* For Subtracting points, Prints new result and starts check */
    this.Points--;
    this.CheckPneumonia()
  }

  CheckTxtResult_1_1() { /* Comparing points to the approproate text for Result and prints */
    switch (this.Points) {
      case 0: this.ResultTxt_1_1_0()
        break;
      case 1: this.ResultTxt_1_1_1()
        break;
      case 2: this.ResultTxt_1_1_2()
        break;
      case 3: this.ResultTxt_1_1_3()
        break;
      case 4: this.ResultTxt_1_1_4()
        break;
      case 5: this.ResultTxt_1_1_4()
        break;
    }
  }

  CheckTxtResult_1_2() { /* Comparing points to the approproate text for Any Infection and prints */
    switch (this.Points) {
      case 0: this.ResultTxt_1_2_0()
        break;
      case 1: this.ResultTxt_1_2_0()
        break;
      case 2: this.ResultTxt_1_2_1()
        break;
      case 3: this.ResultTxt_1_2_1()
        break;
      case 4: this.ResultTxt_1_2_2()
        break;
      case 5: this.ResultTxt_1_2_2()
        break;
    }
  }

  CheckS_1_1() { /* Comparing points and prints "S" if approproate */
    switch (this.Points) {
      case 1: document.getElementById("Point").innerHTML = "Point";
        break;
      default: document.getElementById("Point").innerHTML = "Points";
        break;
    }
  }

  ResultTxt_1_1_0() { /* Pneumonia text for 0 Points */
    document.getElementById("Mortality_1_1").innerHTML = "Low risk group: 0.6% 30-day mortality.";
    document.getElementById("Treatment_1_1").innerHTML = "Consider outpatient treatment.";
  }

  ResultTxt_1_1_1() { /* Pneumonia text for 1 Point */
    document.getElementById("Mortality_1_1").innerHTML = "Low risk group: 2.7% 30-day mortality.";
    document.getElementById("Treatment_1_1").innerHTML = "Consider outpatient treatment.";
  }

  ResultTxt_1_1_2() { /* Pneumonia text for 2 Points */
    document.getElementById("Mortality_1_1").innerHTML = "Moderate risk group: 6.8% 30-day mortality.";
    document.getElementById("Treatment_1_1").innerHTML = "Consider inpatient treatment or outpatient with close followup.";
  }

  ResultTxt_1_1_3() { /* Pneumonia text for 3 Points */
    document.getElementById("Mortality_1_1").innerHTML = "Severe risk group: 14.0% 30-day mortality.";
    document.getElementById("Treatment_1_1").innerHTML = "Consider inpatient treatment with possible intensive care admission.";
  }

  ResultTxt_1_1_4() { /* Pneumonia text for 4-5 Points */
    document.getElementById("Mortality_1_1").innerHTML = "Highest risk group: 27.8% 30-day mortality.";
    document.getElementById("Treatment_1_1").innerHTML = "Consider inpatient treatment with possible intensive care admission.";
  }


  ResultTxt_1_2_0() { /* Any Infection text for 0-1 Points */
    document.getElementById("Mortality_1_1").innerHTML = "Low risk group: < 5% mortality.";
    document.getElementById("Treatment_1_1").innerHTML = "Consider outpatient treatment.";
  }

  ResultTxt_1_2_1() { /* Any Infection text for 2-3 Points */
    document.getElementById("Mortality_1_1").innerHTML = "Moderate risk group: < 10% mortality.";
    document.getElementById("Treatment_1_1").innerHTML = "Consider inpatient treatment or outpatient with close followup.";
  }

  ResultTxt_1_2_2() { /* Any Infection text for 4-5 Points */
    document.getElementById("Mortality_1_1").innerHTML = "Highest risk group: 15-30% mortality.";
    document.getElementById("Treatment_1_1").innerHTML = "Consider inpatient treatment with possible intensive care admission.";
  }
}

